<!-- 主框架 -->
<template>
  <div class="main">
    <el-container>
      <el-header style="
					padding-top: 10px;
					display: flex;
					justify-content: space-between;
				">
        <div> </div>
        <div v-if="rout == '/home/peach'||rout == '/home/special'">
          <router-link :to="{path: '/home/pay',query:{name:rout}}">
            <el-button type="danger">支付</el-button>
          </router-link>
        </div>
        <div v-if="rout == '/home/pay'  ">
          <el-button
            type="danger"
            @click="$router.go(-1)"
          >回退</el-button>
        </div>
      </el-header>
      <el-container>
        <el-aside width="200px">
          <el-menu
            :default-active="activePath"
            background-color="#0b172f"
            text-color="#fff"
            active-text-color="#ff5555"
            :unique-opened="true"
            :router="true"
          >
            <el-submenu
              :index="menuListData.path"
              v-for="menuListData in menuListDatas"
              :key="menuListData.id"
            >
              <template slot="title">
                <span>{{ menuListData.title }}</span>
              </template>
              <el-menu-item
                :index="menuList.path"
                v-for="menuList in menuListData.children"
                :key="menuList.id"
              >
                {{ menuList.title }}
              </el-menu-item>
            </el-submenu>
          </el-menu>
        </el-aside>
        <el-main>
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';

export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data () {
    //这里存放数据
    return {
      rout: null,
      activePath: "/home/peach",
      menuListDatas: [
        {
          id: 1,
          title: "鬼谷Mod站",
          path: "/home/peach",
          children: [
     
            {
              id: 1002,
              path: "/home/special",
              title: "鸯娆整合-鬼谷八荒天元山整合包",
            },
          ],
        },
      ],
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {
    $route () {
      this.rout = this.$route.path
    }
  },
  //方法集合
  methods: {},
  mounted () {
    this.activePath = this.$route.path;
  },
};
</script>
<style lang="less">
.main {
  width: 100%;
  height: 100%;
  background: pink;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;

  .el-container {
    width: 100%;
    height: 100%;

    .el-header {
      background: #182c47;
      color: #fff;
    }

    .el-aside {
      height: 100%;
      background: #0b172f;

      .el-menu {
        border-right: solid 0px #e6e6e6;
      }
    }

    .el-main {
      width: 100%;
      height: 100%;
      background: #020e24;
      color: #fff;
    }
  }
}
</style>
